.logoutbutton {
  height: 30px;
  background-color: rgb(255, 104, 104);
  border-style: solid;
  border-color: rgb(255, 108, 108);
  border-radius: 3px;
  font-family: "Figtree";
  font-size: 17px;
  box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.logoutbutton:hover {
  background-color: rgb(253, 86, 86);
}

.sitepositionindicator {
  background-color: rgb(244, 244, 244);
  border-radius: 7px;
  padding: 10px;
  font-family: "Figtree";
  font-size: 14px;
  font-weight: 300;
  display: flex;
  margin-top: 35.5px;
}
.sitepositionindicator p {
  margin: 0;
}
.sitepositionindicator span:hover {
  text-decoration: underline;
}
.sitepositionindicator svg {
  margin-left: 7px;
  margin-right: 7px;
  font-weight: 300;
  font-size: 12px;
}

nav {
  background-color: white;
  padding: 10px;
  width: 100%;
  position: sticky;
  top: 0;
  display: flex;
  justify-content: space-between;
}
nav h1 {
  width: 100%;
  text-align: center;
}
nav .navbarbuttonscontainer {
  display: flex;
  justify-content: flex-end;
}
nav .navbarbuttonscontainer .navprofilebuttons {
  display: flex;
  margin-right: 10px;
  float: right;
}
nav .navbarbuttonscontainer .navprofilebuttons:hover p {
  text-decoration: underline;
}
nav .navbarbuttonscontainer .navprofilebuttons:hover img {
  border-color: #afafaf;
}
nav .navbarbuttonscontainer .navprofilebuttons img {
  border-radius: 25px;
  margin-right: 5px;
  border-style: solid;
  border-color: #d3d3d3;
}
nav .navbarbuttonscontainer .navprofilebuttons p {
  font-size: 17px;
  font-family: "Figtree";
  line-height: 1.75;
}

